@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
:root {
  --color-white-1: #ffffff;
  --color-white-2: #fcfcfc;
  --color-gray-1: #d6d6d6;
  --color-gray-2: #ececec;
  --color-gray-3: #eaeaea;
  --color-gray-4: #979797;
  --color-gray-5: #828282;
  --color-gray-6: #767676;
  --color-gray-7: #6b6b6b;
  --color-gray-8: #606060;
  --color-black-1: #343434;
  --color-black-2: #3b3b3b;
  --color-black-3: #2e2e2e;
  --color-blue-1: #1797ff;
  --color-blue-2: #1890ff;
  --color-red-1: #dd0031;
}

* {
  font-family: "Roboto", sans-serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container {
  width: 90%;
  margin: 0 auto;
}

.loading__container {
  height: calc(100vh - 114px);
  /** Grid CSS styles **/
  display: grid;
  place-items: center;
}

.tab-section {
  padding: 50px 0;
  /** Grid CSS styles **/
  display: grid;
  place-items: center;
}

.select-section {
  padding: 0 0 20px 0;
}

.navigator-section {
  padding: 60px 0;
  /** Grid CSS styles **/
  display: grid;
  place-items: center;
}

@media (min-width: 768px) {
  .container {
    width: 80%;
  }

  .tab-section {
    padding: 70px 0;
  }

  .select-section {
    padding: 0 0 30px 0;
  }

  .navigator-section {
    padding: 100px 0;
  }
}

.header {
  height: 114px;
  box-shadow: 0 1px 4px 0 rgba(0, 21, 41, 0.12);
  background-image: linear-gradient(to bottom, #ececec -32%, #fff 124%);
}

.header__container {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.loader {
  border: 16px solid var(--color-gray-2);
  border-top: 16px solid var(--color-blue-2);
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.tab {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.tab__item {
  width: 100px;
  background-color: var(--color-white-1);
  color: var(--color-gray-7);
  padding: 2px 0;
  border: 1px solid var(--color-gray-1);
  border-radius: 2px;
  cursor: pointer;
  transition: 0.4s all;
  /** Font styles **/
  font-size: 16px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.75;
  letter-spacing: normal;
  text-align: center;
}

.tab__item--left {
  border-right: none;
  border-radius: 2px 0 0 2px;
}

.tab__item--right {
  border-radius: 0 2px 2px 0;
}

.tab__item:hover {
  color: var(--color-blue-1);
  border: 1px solid var(--color-blue-1);
}

.tab__item--left:hover {
  border-right: none;
}

.tab__item--left:hover .tab__item--right {
  border-left: 1px solid var(--color-blue-1);
}

.tab__item--left--focused {
  color: var(--color-blue-1);
  border: 1px solid var(--color-blue-1);
  border-right: none;
}

.tab__item--right--focused {
  color: var(--color-blue-1);
  border: 1px solid var(--color-blue-1);
}

/* Usamos !important muchas veces, pues al ser un componente de una librer√≠a */
/* de React, no tenemos el control total de las clases generadas.*/
.react-select-container {
  width: 100%;
  border-radius: 4px;
  border: solid 1px var(--color-gray-4) !important;
  cursor: pointer !important;
}

.react-select-container:focus {
  outline: none !important;
  border: solid 1px var(--color-black-3);
}

.react-select__control {
  border: none !important;
  cursor: pointer !important;
  /** Flexbox styles **/
  display: flex;
  flex-direction: row;
  align-content: center;
}

.react-select__menu {
  background-color: var(--color-white-1);
  border: none;
  border-radius: 0 !important;
  box-shadow: 0 2px 2px 0 var(--color-gray-3);
}

.react-select__value-container {
  height: 32px;
  margin: 0;
  /** Font styles **/
  color: var(--color-black-1);
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.57;
  letter-spacing: normal;
  /** Flexbox styles **/
  display: flex;
  flex-direction: row;
  align-content: center;
}

.react-select__indicators {
  height: 32px;
}

.react-select__indicator-separator {
  display: none;
}

.react-select__indicator svg {
  width: 16px;
  color: var(--color-black-3);
}

.react-select__menu-list {
  padding: 0 !important;
}

.react-select__option {
  padding: 12px !important;
  cursor: pointer !important;
  /** Flexbox styles **/
  display: flex !important;
  flex-direction: row;
  align-items: center;
  grid-gap: 13px;
  gap: 13px;
  /** Font styles **/
  font-size: 14px !important;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.57;
  letter-spacing: normal;
}

.react-select__option img {
  width: 24px;
}

@media (min-width: 768px) {
  .react-select-container {
    width: 240px;
  }
}

.cardlist {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  place-items: center;
  grid-gap: 10px;
  gap: 10px;
}

@media (min-width: 768px) {
  .cardlist {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px 40px;
    gap: 30px 40px;
  }
}

.card {
  height: 100%;
  width: 100%;
  min-width: 280px;
  background-color: var(--color-white);
  border-radius: 6px;
  border: solid 1px var(--color-gray-4);
  text-decoration: none;
  cursor: pointer;
  transition: 0.4s all;
  /** Flexbox styles **/
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.card:hover {
  opacity: 0.45;
}

.card__content {
  max-width: 75%;
  padding: 26px;
  /** Flexbox styles **/
  display: flex;
  flex-direction: column;
  grid-gap: 6px;
  gap: 6px;
}

.card__fave {
  width: 60px;
  min-width: 60px;
  background-color: var(--color-gray-3);
  border-radius: 0 6px 6px 0;
  /** Grid CSS styles **/
  display: grid;
  place-items: center;
}

.card__content__time {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 8px;
  gap: 8px;
}

.card__content__time__icon {
  width: 16px;
}

.card__content__time__text {
  color: var(--color-gray-6);
  font-size: 11px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
}

.card__content__title {
  color: var(--color-gray-7);
  font-size: 14px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: 0.25px;
}

.card__fave__icon {
  cursor: pointer;
  transition: 0.4s all;
}

.card__fave__icon:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.pagination {
  display: flex;
  align-items: center;
  grid-gap: 8px;
  gap: 8px;
}

.button {
  background-color: var(--color-white-1);
  width: 32px;
  height: 32px;
  border: solid 1px var(--color-gray-1);
  border-radius: 6px;
  cursor: pointer;
  transition: 0.4s all;
  /** Font styles **/
  color: var(--color-gray-7);
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.57;
  letter-spacing: normal;
  text-align: center;
}

.button:hover {
  background-color: var(--color-blue-2);
  border: solid 1px var(--color-blue-2);
  /** Font styles **/
  color: var(--color-white-1);
}

.button--disabled {
  background-color: var(--color-gray-1);
  color: var(--color-white-2);
}

.button--disabled:hover {
  background-color: var(--color-gray-1);
  border: 1px solid var(--color-gray-1);
  /** Font styles **/
  color: var(--color-white-2);
}

.button--focused {
  background-color: var(--color-blue-2);
  border: solid 1px var(--color-blue-2);
  /** Font styles **/
  color: var(--color-white-1);
}

